#header-container {
  margin-top: 90px;
}

#header-text {
  font-size: 3em;
}

@media only screen and (min-width: 992px) {
  #header-container {
    margin-top: 100px;
  }
}

#search-bar-container {
  margin-top: 50px;
}

#search-bar {
  width: 300px;
  height: 60px;
  font-size: 1.5em;
}

#search-button-container {
  margin-top: 50px;
}

#search-button {
  height: 60px;
  width: 150px;
  font-size: 1.75em;
}

#error-container {
  position: relative;
  top: 40px;
  height: 50px;
  text-align: center;
}

.warning-message {
  width: 300px;
}

html,
body {
  height: 100%;
  width: 100%;
  color: #ffffff;
  background-color: #1d2228;
}

#root {
  height: 100%;
}

#home-container {
  margin: 0 !important;
  height: 100% !important;
  width: 100vw;
  background-image: url(/static/media/music-concert.19bf476b.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
}

.track-list-container {
  margin-top: 60px;
}

.centered-header {
  text-align: center;
  text-transform: capitalize;
}

.track-item {
  margin: 60px auto !important;
  border-bottom: 2px solid #ffffff;
  padding-bottom: 40px !important;
  width: 100%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.track-item:hover {
  background-color: #f26202;
  box-shadow: 0 10px 5px #000000;
  border: none;
  border-radius: 8px;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

@media (min-width: 992px) {
  .track-item {
    width: 35%;
  }
}

